import React from 'react';
import './Cards.css';
import CardItem from './CardItem';



function Cards() {
  return (
    <div className='cards'>
      <h1>Projects</h1>
      <div className='cards__container'>
        <div className='cards__wrapper'>
          
          <ul className='cards__items'>
            <CardItem
              src='images/reactbg.png'
              text='Travel Website'
              label='React'
              class='social-icon-link github'
              
              
            />
            <CardItem
              src='images/ror.jpg'
              text='Fitness Tracker website'
              label='Ruby on Rails'
              
              
            />
            
          </ul>
      
          <ul className='cards__items'>
            <CardItem
              src='images/reactbg.png'
              text='Portfolio website : A portolio website who display projects and a contact page'
              label='React'
              
            />
            <CardItem
              src='images/ror.jpg'
              text='Bike race website : this website implement Flickr API and use also Reac for the Front end'
              label='Ruby on rails'
               
            />
            <CardItem
              src='images/boots.jpg'
              text='Event website : A Static website who use Bootstrap framework and HTML/CSS'
              label='Html/Css/Bootstrap'
               
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
