import React from 'react'
import './Resume.css'

function Resume() {
    return(

        <div className='footer-container'>
        <section className='resumecadre'>
        <div className='social-media-wrap'>
        
        <div className="container">
          <div className="row">
            
              <h2 className='titleh2'>About me</h2>

              <div className='profilewrapper'>
            <img className='profilepic' src='images/clpz.png' alt='profile'/>

              </div>
              
              <p className='intro'>Hello! I'm Chris, a Junior Full Stack Developer, a Ruby and react enthusiast, and a guy slighty obsessed for code quality. I’m currently available for freelance work. If you have a project that you want to get started or think you need my help with something, then get in touch with me.</p>
              <div className='info-list'>
                  <ul className='list1'>
                      <li><strong>Experience</strong></li><span>Junior</span>
                 </ul>
                 <ul className='list1'>
                      <li><strong>Dev Skills</strong></li><span>React, Ruby on Rails, Javascript</span>
                 </ul>
                 <ul className='list1'>
                      <li><strong>Residence</strong></li><span>Barcelona,Spain</span>
                 </ul>
                 <ul className='list1'>
                      <li><strong>E-mail</strong></li><span>christophelopez.rb@gmail.com</span>
                 </ul>
                    
              </div>
          </div>
        </div>
      
      </div>
      </section>
      </div> 
    )
      
}

export default Resume
